.tilted-card-figure {
  position: relative;
  width: 100%;
  height: 100%;
  perspective: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tilted-card-mobile-alert {
  position: absolute;
  top: 1rem;
  text-align: center;
  font-size: 0.875rem;
  color: #D0D0D0; /* Light gray */
  display: none;
}

.tilted-card-error {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #D0D0D0; /* Light gray */
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  background: #505050; /* Medium gray */
  border-radius: 15px;
}

@media (max-width: 640px) {
  .tilted-card-mobile-alert {
    display: block;
  }
  .tilted-card-caption {
    display: none;
  }
}

.tilted-card-inner {
  position: relative;
  transform-style: preserve-3d;
}

.tilted-card-img {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  border-radius: 15px;
  will-change: transform;
  transform: translateZ(0);
  border: 2px solid #808080; /* Medium gray */
}

.tilted-card-overlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  will-change: transform;
  transform: translateZ(30px);
}

.tilted-card-caption {
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 4px;
  background-color: #D0D0D0; /* Light gray */
  padding: 4px 10px;
  font-family: 'Montserrat', sans-serif; /* Changed to Montserrat */
  font-size: 10px;
  color: #1E1E1E; /* Dark gray */
  opacity: 0;
  z-index: 3;
}